// app/(dashboard)/dashboard/schedule/page.tsx
"use client"

import { useState, useEffect } from "react"
import Link from "next/link"
import { supabase } from "@/lib/supabaseClient"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog"
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog"
import {
  Calendar,
  Plus,
  Trash2,
  Clock,
  ArrowLeft,
} from "lucide-react"
import type { ScheduleSlot } from "@/types/database.types"

// ÊôÇÊÆµË®≠ÂÆö
const SLOTS = [
  { slot: 1, start: "08:00", end: "08:50" },
  { slot: 2, start: "09:00", end: "09:50" },
  { slot: 3, start: "10:00", end: "10:50" },
  { slot: 4, start: "11:00", end: "11:50" },
  { slot: 5, start: "12:00", end: "12:50" },
  { slot: 6, start: "13:00", end: "13:50" },
  { slot: 7, start: "14:00", end: "14:50" },
  { slot: 8, start: "15:00", end: "15:50" },
  { slot: 9, start: "16:00", end: "16:50" },
  { slot: 10, start: "17:00", end: "17:50" },
]

// ÊòüÊúüË®≠ÂÆö
const DAYS = [
  { day: 1, label: "ÈÄ±‰∏Ä" },
  { day: 2, label: "ÈÄ±‰∫å" },
  { day: 3, label: "ÈÄ±‰∏â" },
  { day: 4, label: "ÈÄ±Âõõ" },
  { day: 5, label: "ÈÄ±‰∫î" },
  { day: 6, label: "ÈÄ±ÂÖ≠" },
  { day: 7, label: "ÈÄ±Êó•" },
]

export default function SchedulePage() {
  const [slots, setSlots] = useState<ScheduleSlot[]>([])
  const [loading, setLoading] = useState(true)

  // Á∑®ËºØÂ∞çË©±Ê°Ü
  const [dialogOpen, setDialogOpen] = useState(false)
  const [editingSlot, setEditingSlot] = useState<Partial<ScheduleSlot> | null>(null)
  const [formData, setFormData] = useState({
    subject_name: "",
    teacher: "",
    location: "",
    note: "",
  })
  const [saving, setSaving] = useState(false)

  // Âà™Èô§Á¢∫Ë™ç
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false)
  const [deletingSlot, setDeletingSlot] = useState<ScheduleSlot | null>(null)

  // ËºâÂÖ•Ë≥áÊñô
  const fetchSlots = async () => {
    const { data: { user } } = await supabase.auth.getUser()
    if (!user) return

    const { data } = await supabase
      .from("schedule_slots")
      .select("*")
      .eq("user_id", user.id)
      .order("day_of_week")
      .order("slot_number")

    setSlots(data || [])
    setLoading(false)
  }

  useEffect(() => {
    fetchSlots()
  }, [])

  // ÂèñÂæóÁâπÂÆöÊ†ºÂ≠êÁöÑË™≤Á®ã
  const getSlot = (day: number, slot: number): ScheduleSlot | undefined => {
    return slots.find(s => s.day_of_week === day && s.slot_number === slot)
  }

  // ÈñãÂïüÁ∑®ËºØÂ∞çË©±Ê°Ü
  const openDialog = (day: number, slot: number) => {
    const existing = getSlot(day, slot)
    if (existing) {
      setEditingSlot(existing)
      setFormData({
        subject_name: existing.subject_name,
        teacher: existing.teacher || "",
        location: existing.location || "",
        note: existing.note || "",
      })
    } else {
      setEditingSlot({ day_of_week: day, slot_number: slot })
      setFormData({
        subject_name: "",
        teacher: "",
        location: "",
        note: "",
      })
    }
    setDialogOpen(true)
  }

  // ÂÑ≤Â≠ò
  const handleSave = async () => {
    if (!formData.subject_name.trim() || !editingSlot) return

    setSaving(true)
    const { data: { user } } = await supabase.auth.getUser()
    if (!user) {
      setSaving(false)
      return
    }

    if (editingSlot.id) {
      // Êõ¥Êñ∞
      await supabase
        .from("schedule_slots")
        .update({
          subject_name: formData.subject_name.trim(),
          teacher: formData.teacher.trim() || null,
          location: formData.location.trim() || null,
          note: formData.note.trim() || null,
        })
        .eq("id", editingSlot.id)
    } else {
      // Êñ∞Â¢û
      await supabase
        .from("schedule_slots")
        .insert({
          user_id: user.id,
          day_of_week: editingSlot.day_of_week!,
          slot_number: editingSlot.slot_number!,
          subject_name: formData.subject_name.trim(),
          teacher: formData.teacher.trim() || null,
          location: formData.location.trim() || null,
          note: formData.note.trim() || null,
        })
    }

    setSaving(false)
    setDialogOpen(false)
    fetchSlots()
  }

  // Âà™Èô§
  const handleDelete = async () => {
    if (!deletingSlot) return

    await supabase
      .from("schedule_slots")
      .delete()
      .eq("id", deletingSlot.id)

    setDeleteDialogOpen(false)
    setDeletingSlot(null)
    fetchSlots()
  }

  // ÈñãÂïüÂà™Èô§Á¢∫Ë™ç
  const openDeleteDialog = (slot: ScheduleSlot) => {
    setDeletingSlot(slot)
    setDeleteDialogOpen(true)
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="w-8 h-8 border-4 border-blue-600 border-t-transparent rounded-full animate-spin" />
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* ËøîÂõûÊåâÈàï */}
      <Link href="/dashboard">
        <Button variant="ghost" size="sm" className="gap-2">
          <ArrowLeft className="w-4 h-4" />
          ËøîÂõûÁ∏ΩË¶Ω
        </Button>
      </Link>

      {/* È†ÅÈù¢Ê®ôÈ°å */}
      <div>
        <h1 className="text-2xl font-bold text-gray-800 flex items-center gap-2">
          <Calendar className="w-7 h-7 text-blue-600" />
          Ë™≤Ë°®ÁÆ°ÁêÜ
        </h1>
        <p className="text-gray-600 mt-1">ÈªûÊìäÊ†ºÂ≠êÊñ∞Â¢ûÊàñÁ∑®ËºØË™≤Á®ã</p>
      </div>

      {/* Ë™≤Ë°®Á∂≤Ê†º */}
      <div className="bg-white rounded-lg shadow-sm border overflow-x-auto">
        <table className="w-full min-w-[800px]">
          <thead>
            <tr className="bg-gray-50 border-b">
              <th className="p-3 text-left text-sm font-semibold text-gray-600 w-24">
                <Clock className="w-4 h-4 inline mr-1" />
                ÊôÇÈñì
              </th>
              {DAYS.map(day => (
                <th key={day.day} className="p-3 text-center text-sm font-semibold text-gray-600">
                  {day.label}
                </th>
              ))}
            </tr>
          </thead>
          <tbody>
            {SLOTS.map(slot => (
              <tr key={slot.slot} className="border-b hover:bg-gray-50">
                {/* ÊôÇÈñìÊ¨Ñ */}
                <td className="p-2 text-sm text-gray-500 border-r bg-gray-50">
                  <div className="font-medium">{slot.start}</div>
                  <div className="text-xs">{slot.end}</div>
                </td>

                {/* ÂêÑÂ§©Ê¨Ñ‰Ωç */}
                {DAYS.map(day => {
                  const cellSlot = getSlot(day.day, slot.slot)
                  return (
                    <td
                      key={day.day}
                      className="p-1 border-r last:border-r-0 align-top"
                    >
                      {cellSlot ? (
                        <div
                          className="group relative p-2 bg-blue-50 border border-blue-200 rounded-lg min-h-[60px] cursor-pointer hover:bg-blue-100 transition-colors"
                          onClick={() => openDialog(day.day, slot.slot)}
                        >
                          <div className="font-medium text-sm text-blue-800 truncate">
                            {cellSlot.subject_name}
                          </div>
                          {cellSlot.teacher && (
                            <div className="text-xs text-blue-600 truncate">
                              {cellSlot.teacher}
                            </div>
                          )}
                          {cellSlot.location && (
                            <div className="text-xs text-gray-500 truncate">
                              üìç {cellSlot.location}
                            </div>
                          )}

                          {/* Âà™Èô§ÊåâÈàï */}
                          <button
                            onClick={(e) => {
                              e.stopPropagation()
                              openDeleteDialog(cellSlot)
                            }}
                            className="absolute top-1 right-1 p-1 rounded bg-red-100 text-red-600 opacity-0 group-hover:opacity-100 transition-opacity hover:bg-red-200"
                          >
                            <Trash2 className="w-3 h-3" />
                          </button>
                        </div>
                      ) : (
                        <div
                          className="min-h-[60px] border border-dashed border-gray-200 rounded-lg cursor-pointer hover:bg-gray-50 hover:border-gray-300 transition-colors flex items-center justify-center"
                          onClick={() => openDialog(day.day, slot.slot)}
                        >
                          <Plus className="w-4 h-4 text-gray-300" />
                        </div>
                      )}
                    </td>
                  )
                })}
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Áµ±Ë®à */}
      <div className="flex gap-4 text-sm text-gray-500">
        <span>ÂÖ± {slots.length} Â†ÇË™≤</span>
        <span>|</span>
        <span>
          {DAYS.map(d => {
            const count = slots.filter(s => s.day_of_week === d.day).length
            return count > 0 ? `${d.label} ${count}Â†Ç` : null
          }).filter(Boolean).join("„ÄÅ") || "Â∞öÁÑ°Ë™≤Á®ã"}
        </span>
      </div>

      {/* Á∑®ËºØÂ∞çË©±Ê°Ü */}
      <Dialog open={dialogOpen} onOpenChange={setDialogOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>
              {editingSlot?.id ? "Á∑®ËºØË™≤Á®ã" : "Êñ∞Â¢ûË™≤Á®ã"}
              {editingSlot && (
                <span className="text-sm font-normal text-gray-500 ml-2">
                  {DAYS.find(d => d.day === editingSlot.day_of_week)?.label}{" "}
                  {SLOTS.find(s => s.slot === editingSlot.slot_number)?.start}
                </span>
              )}
            </DialogTitle>
            <DialogDescription>
              Â°´ÂØ´Ë™≤Á®ãË≥áË®äÔºåÂè™ÊúâÁßëÁõÆÂêçÁ®±ÁÇ∫ÂøÖÂ°´
            </DialogDescription>
          </DialogHeader>

          <div className="space-y-4 py-4">
            <div className="space-y-2">
              <Label>ÁßëÁõÆÂêçÁ®± *</Label>
              <Input
                value={formData.subject_name}
                onChange={(e) => setFormData({ ...formData, subject_name: e.target.value })}
                placeholder="‰æãÂ¶ÇÔºöÂúãÊñá„ÄÅÊï∏Â≠∏"
              />
            </div>
            <div className="space-y-2">
              <Label>ÊéàË™≤ËÄÅÂ∏´</Label>
              <Input
                value={formData.teacher}
                onChange={(e) => setFormData({ ...formData, teacher: e.target.value })}
                placeholder="ÈÅ∏Â°´"
              />
            </div>
            <div className="space-y-2">
              <Label>‰∏äË™≤Âú∞Èªû</Label>
              <Input
                value={formData.location}
                onChange={(e) => setFormData({ ...formData, location: e.target.value })}
                placeholder="‰æãÂ¶ÇÔºöA101ÊïôÂÆ§"
              />
            </div>
            <div className="space-y-2">
              <Label>ÂÇôË®ª</Label>
              <Input
                value={formData.note}
                onChange={(e) => setFormData({ ...formData, note: e.target.value })}
                placeholder="ÈÅ∏Â°´"
              />
            </div>
          </div>

          <DialogFooter>
            <Button variant="outline" onClick={() => setDialogOpen(false)}>
              ÂèñÊ∂à
            </Button>
            <Button
              onClick={handleSave}
              disabled={!formData.subject_name.trim() || saving}
              className="bg-blue-600 hover:bg-blue-700"
            >
              {saving ? "ÂÑ≤Â≠ò‰∏≠..." : "ÂÑ≤Â≠ò"}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      {/* Âà™Èô§Á¢∫Ë™çÂ∞çË©±Ê°Ü */}
      <AlertDialog open={deleteDialogOpen} onOpenChange={setDeleteDialogOpen}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Á¢∫ÂÆöË¶ÅÂà™Èô§ÂóéÔºü</AlertDialogTitle>
            <AlertDialogDescription>
              Âà™Èô§„Äå{deletingSlot?.subject_name}„ÄçÂæåÁÑ°Ê≥ïÂæ©Âéü„ÄÇ
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>ÂèñÊ∂à</AlertDialogCancel>
            <AlertDialogAction
              onClick={handleDelete}
              className="bg-red-600 hover:bg-red-700"
            >
              Á¢∫ÂÆöÂà™Èô§
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </div>
  )
}
